.LayoutBlock{
    display: flex;
    justify-content: center;
    height: 73vh;
    width: 35vh;
    margin: 0 auto;
    margin-top: 10vh;
    border-radius: 7px;
    -webkit-box-shadow: 0px 13px 38px 0px rgba(0,0,0,0.19);
    -moz-box-shadow: 0px 13px 38px 0px rgba(0,0,0,0.19);
    box-shadow: 0px 13px 38px 0px rgba(0,0,0,0.19);
    padding: 0 2vh;
}
@media all and (max-width: 767px){
    .LayoutBlock {
      height: 100%;
      width: 100vw;
      margin: 0;
      padding: 0;
    }
  }